@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  background-color: black;
  background: rgb(123,143,148);
  background: repeating-linear-gradient(45, rgba(123,143,148,1) 0%, rgba(108,114,115,1) 48%, rgba(123,143,148,1) 100%);
}

.vh-50 {
  height: 50vh;
}

.ph-1r {
  padding: 0rem 1rem;
}

.active-nav-link {
  color: #fff!important;
}
/* blinking cursor */
#cursor {
  background: #00bc8c;
  display: inline-block;
  line-height: 20px;
  margin-left: 3px;
  animation: blink 1.25s infinite;
  width: 3px;
  height: 41px;
  position: relative;
  bottom: -3px;
}
@keyframes blink {
  0% {
    background: #222;
  }
  50% {
    background: #00bc8c;
  }
  100% {
    background: #222;
  }
}

.review-preview{max-height:200px;display:flex;cursor:pointer;border:1px solid rgba(0,0,0,0.125);border-radius:0.25rem;border-color:#00bc8c;padding:1rem 1rem;max-width:800px;margin:auto;margin-top:2rem}.review-preview img{width:300px;border-radius:0.25rem}.review-preview .content{margin:0rem 2rem}.link{color:white}.link:hover{color:white;text-decoration:none}

.hero{height:50vh}.jumbotron{min-height:350px;max-height:400px;padding-bottom:0}.section{max-width:1200px;margin:auto;margin-bottom:2rem;display:flex;flex-wrap:wrap;justify-content:center}.recent-posts{display:flex;flex-wrap:wrap;justify-content:space-evenly;max-width:1200px;margin:auto}@media screen and (min-width: 700px){.about-text{width:50%}}

.post{background-color:#303030;background-clip:border-box;border:1px solid rgba(0,0,0,0.125);border-radius:.25rem;border-color:#00bc8c !important}.blog-posts-list{display:flex;flex-direction:column;align-items:flex-end}.post-img{border-radius:.25rem;border:1px solid rgba(0,0,0,0.125)}.img-container{border:1px solid rgba(0,0,0,0.125);border-radius:.25rem}@media screen and (min-width: 700px){.post{margin-bottom:1rem;padding:1rem;width:60%;display:flex;height:300px}.post .img-container{height:200px;align-self:center}.post .img-container .post-img{height:100%}.post .post-content-preview{height:250px;padding:10px;align-self:center}.post .post-content-preview p{line-height:30px}}


.header{z-index:1000;position:fixed;left:0;right:0;height:50px;display:flex;align-items:center;padding:0rem 2rem;justify-content:space-between;background-color:#222;box-shadow:0px -10px 16px 0px}.brand-container{height:100%;display:flex;align-items:center}.brand-logo{height:90%}.brand-name{padding:0rem 0.5rem}.link{padding:0rem 1rem}.spacer{height:50px;width:100vw}

.reviews-container{border:1px solid rgba(0,0,0,0.125);border-radius:0.25rem;border-color:#00bc8c;padding:1rem 1rem;max-width:800px;margin:auto;margin-top:2rem}

body{background-color:#222}.navbar{padding:0.25rem 1rem}.name{color:#00bc8c;opacity:.75}

