.review-preview {
  max-height: 200px;
  display: flex;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  border-color: #00bc8c;
  padding: 1rem 1rem;
  max-width: 800px;
  margin: auto;
  margin-top: 2rem;

  & img {
    width: 300px;
    border-radius: 0.25rem;
  }

  & .content {
    margin: 0rem 2rem;
  }
}

.link {
  color: white;
  &:hover {
    color: white;
    text-decoration: none;
  }
}
